<template>
  <div ref="mainPanel" class="mainPanel">
    <template v-if="stockId">
      <div class="tablePanel" :style="{ height: tableHeight + 'px' }">
        <el-table :data="currentRow.parts" style="width: 100%" height="100%" ref="mStock" class="tableGrow">
          <el-table-column prop="name" label="备件名称" show-overflow-tooltip min-width="140" />
          <el-table-column prop="apply" :label="stockType[currentRow.type] + '数量'" min-width="160" />
          <el-table-column prop="classify" show-overflow-tooltip label="备件类别" min-width="140">
            <template slot-scope="scope">{{ classify[scope.row.classify] }}</template>
          </el-table-column>
        </el-table>
      </div>
      <div class="approveBtns" ref="approveBtns" v-if="currentRow.state == 5 && tid != ''">
        <el-button size="small" type="primary" @click="agreeApply(currentRow)">同 意</el-button>
        <el-button size="small" type="primary" @click="refuseApply(currentRow)">拒 绝</el-button>
      </div>
    </template>
    <el-empty v-else :image-size="200"></el-empty>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import array from "lodash/array";
import { parseTime } from "@/utils/index";
export default {
  data() {
    return {
      stockId: "",
      currentRow: null,
      allStockListTemp: [],
      stockType: {
        1: "申领",
        2: "入库",
        3: "出库",
      },
      tableHeight: 0,
      pid: "",
      tid: "",
    };
  },
  created() {
    if (this.$route.query && this.$route.query.ddIngPid) {
      this.pid = this.$route.query.ddIngPid;
    }
    if (this.$route.query && this.$route.query.ddIngTid) {
      this.tid = this.$route.query.ddIngTid;
    }
    if (this.$route.query && this.$route.query.stockId) {
      this.stockId = this.$route.query.stockId;
      this.allStockListTemp = [
        ...this.allStockInList,
        ...this.allStockOutList,
        ...this.applyingList,
      ].sort((a, b) => {
        return new Date(b.createTime) - new Date(a.createTime);
      });
      this.searchById();
    }
  },
  mounted() {
    if (this.currentRow.state == 5) {
      this.tableHeight =
        this.$refs.mainPanel.clientHeight -
        this.$refs.approveBtns.clientHeight -
        300;
      window.onresize = () => {
        return (() => {
          this.tableHeight =
            this.$refs.mainPanel.clientHeight -
            this.$refs.approveBtns.clientHeight -
            300;
        })();
      };
    } else {
      this.tableHeight = this.$refs.mainPanel.clientHeight - 50;
      window.onresize = () => {
        return (() => {
          this.tableHeight = this.$refs.mainPanel.clientHeight - 50;
        })();
      };
    }
  },
  beforeDestroy() {
    window.onresize = null;
  },
  computed: {
    ...mapGetters([
      "classify",
      "allStockList",
      "mimeStockList",
      "allStockInList",
      "allStockOutList",
      "applyingList",
      "process_code",
      "userInfo",
      "process_instance_id",
      "corpId",
    ]),
  },
  methods: {
    searchById() {
      this.currentRow = this.allStockListTemp.filter(
        (i) => i.id == this.stockId
      )[0];
    },
    agreeApply(row) {
      this.$confirm("确认同意该操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.updateTask(1);
          let stockNames = [];
          if (row.type == 1) {
            for (let item of row.parts) {
              let allStockIndex = this.allStockList.findIndex(
                (i) => i.id == item.id
              );
              let mimeStockIndex = this.mimeStockList.findIndex(
                (i) => i.id == item.id
              );
              if (this.allStockList[allStockIndex].normal < item.apply) {
                stockNames.push(this.allStockList[allStockIndex].name);
              } else {
                this.$store.commit("demo/UPDATE_ALLSTOCKLIST_NORMAL", {
                  id: this.allStockList[allStockIndex].id,
                  normal: this.allStockList[allStockIndex].normal - item.apply,
                });
                this.$store.commit("demo/UPDATE_ALLSTOCKLIST_MIMESTOCKNUM", {
                  id: this.allStockList[allStockIndex].id,
                  apply: item.apply,
                });
                if (mimeStockIndex != -1) {
                  this.$store.commit("demo/UPDATE_MIMESTOCKLIST_NORMAL", {
                    id: this.mimeStockList[mimeStockIndex].id,
                    normal:
                      Number(this.mimeStockList[mimeStockIndex].normal) +
                      Number(item.apply),
                  });
                } else {
                  let data = JSON.parse(
                    JSON.stringify(this.allStockList[allStockIndex])
                  );
                  data["normal"] = item.apply;
                  this.$store.commit("demo/ADD_MIMESTOCKLIST", data);
                }
              }
            }
          } else if (row.type == 2) {
            for (let item of row.parts) {
              let allStockIndex = this.allStockList.findIndex(
                (i) => i.id == item.id
              );
              this.$store.commit("demo/UPDATE_ALLSTOCKLIST_NORMAL", {
                id: this.allStockList[allStockIndex].id,
                normal:
                  Number(this.allStockList[allStockIndex].normal) +
                  Number(item.apply),
              });
            }
          } else {
            for (let item of row.parts) {
              let allStockIndex = this.allStockList.findIndex(
                (i) => i.id == item.id
              );
              if (this.allStockList[allStockIndex].normal < item.apply) {
                stockNames.push(this.allStockList[allStockIndex].name);
              } else {
                this.$store.commit("demo/UPDATE_ALLSTOCKLIST_NORMAL", {
                  id: this.allStockList[allStockIndex].id,
                  normal: this.allStockList[allStockIndex].normal - item.apply,
                });
              }
            }
          }
          this.$store.commit("demo/UPDATE_STOCKLISTSTATE", {
            id: row.id,
            type: row.type,
            state: 2,
          });
          if (stockNames.length > 0) {
            this.$message.error(
              stockNames.toString() + " 库存不足，操作失败！"
            );
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    refuseApply(row) {
      this.$confirm("确认拒绝该操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.updateTask(2);
          this.$store.commit("demo/UPDATE_STOCKLISTSTATE", {
            id: row.id,
            type: row.type,
            state: 3,
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    updateTask(type = 1) {
      this.$http
        .post("/biz/updateTodoStatus", {
          processInstanceId: this.process_instance_id,
          //只传一个数组对象
          tasks: [
            {
              taskId: this.tid,
              status: "COMPLETED",
              result: type == 1 ? "AGREE" : "REFUSE",
            },
          ],
        })
        .then((res) => {
          if (res.success) {
          }
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.approveBtns {
  display: flex;
  justify-content: center;
  padding: 20px;
}
</style>